* {
  box-sizing: border-box;
  font-family: "Work sans";
}
*::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background: #fff;
  color: #4d5974;
  display: flex;
  min-height: 100vh;
  background: top no-repeat url("/assets/images/background-pattern-desktop.svg");
  background-color: hsl(275, 100%, 97%);
  justify-content: center;
  align-items: center;
  background-size: 100%;
}
@media (max-width: 767.98px) {
  body {
    padding: 0 10px;
  }
}

.container {
  margin: 0 auto;
  padding: 4rem;
  width: 48rem;
  height: max-content;
  background-color: hsl(0, 0%, 100%);
  border: none;
  border-radius: 2.5%;
}
@media (max-width: 767.98px) {
  .container {
    padding: 0.5rem;
  }
}
.container header {
  display: flex;
  gap: 20px;
  align-items: center;
  color: hsl(292, 42%, 14%);
}
.container header h2 {
  font-size: 24px;
  font-weight: bold;
}
@media (min-width: 1204px) {
  .container header h2 {
    font-size: 48px;
    font-weight: bold;
  }
}
@media ((min-width: 768px) and (max-width: 991.98px)) {
  .container header h2 {
    font-size: 32px;
    font-weight: bold;
  }
}

.accordion .accordion-item button[aria-expanded=true] {
  border-bottom: 1px solid hsl(292, 42%, 14%);
}
.accordion button {
  position: relative;
  display: block;
  text-align: left;
  width: 100%;
  padding: 1em 0;
  color: #7288a2;
  font-size: 1.15rem;
  font-weight: 400;
  border: none;
  background: none;
  outline: none;
  color: hsl(292, 16%, 49%);
}
@media (max-width: 767.98px) {
  .accordion button {
    padding: 1em 0.5em;
  }
}
.accordion button .icon {
  background-color: hsl(292, 16%, 49%);
  border: 1px solid hsl(292, 16%, 49%);
}
.accordion button:hover, .accordion button:focus {
  cursor: pointer;
  color: hsl(292, 42%, 14%);
}
.accordion button:hover .icon, .accordion button:focus .icon {
  background-color: hsl(292, 42%, 14%);
}
.accordion button:hover::after, .accordion button:focus::after {
  cursor: pointer;
  color: hsl(292, 42%, 14%);
  border: 1px solid hsl(292, 42%, 14%);
}
.accordion button .accordion-title {
  padding: 1em 1.5em 1em 0;
}
.accordion button .icon {
  display: inline-block;
  position: absolute;
  top: 18px;
  right: 0px;
  width: 22px;
  height: 22px;
  border-radius: 22px;
  margin-left: 5px;
}
.accordion button .icon::before {
  display: block;
  position: absolute;
  content: "";
  top: 9px;
  left: 5px;
  width: 10px;
  height: 2px;
  background: hsl(0, 0%, 100%);
}
.accordion button .icon::after {
  display: block;
  position: absolute;
  content: "";
  top: 5px;
  left: 9px;
  width: 2px;
  height: 10px;
  background: hsl(0, 0%, 100%);
}
.accordion button[aria-expanded=true] {
  color: hsl(292, 42%, 14%);
}
.accordion button[aria-expanded=true] .icon::after {
  width: 0;
}
.accordion button[aria-expanded=true] + .accordion-content {
  opacity: 1;
  max-height: 9em;
  transition: all 200ms linear;
  will-change: opacity, max-height;
}
.accordion .accordion-content {
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  transition: opacity 200ms linear, max-height 200ms linear;
  will-change: opacity, max-height;
}
.accordion .accordion-content p {
  font-size: 1rem;
  font-weight: 300;
  margin: 2em 0;
}